@mixin hover-btn($speed: 0.2s) {
    transition: transform $speed;

    &:hover,
    &:focus {
        transform: scale(1.2);
    }

    @content;
}

.planet-preview {
    box-shadow: 0 0 0px 1px #00000040;

    position: relative;
    border-radius: 8px;
    padding: 30px 20px;
    // min-width: 250px;
    // height: 330px;
    height: 100%;
    width: 100%;
    display: flex;
    flex-direction: column;
    background-color: #1010108c;
    // justify-content: space-between;

    .img-container {
        height: 60%;
        width: 100%;
        img {
            height: 100%;
            width: 100%;
            object-fit: cover;
            cursor: pointer;
        }
    }

    .preview-details {
        height: 40%;
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        width: 100%;

        p {
            margin-block: 5px;
            text-shadow: 0 0 0.5px black;
            width: 100%;
        }

        a {
            cursor: pointer;
            padding-block: 5px;
            color: inherit;
            text-decoration: none;
        }
    }

    &:hover .preview-details a {
        text-decoration: underline;
    }

    .remove-planet {
        display: none;
        position: absolute;
        padding: 5px 10px;
        top: -7px;
        right: -7px;
        border: none;
        border-radius: 2px;
        background-color: #0b0b0b;
        box-shadow: 0 0 1px 0.1px white;
        color: rgb(225, 225, 225);
        transition: box-shadow .2s;

        // &:hover {
        //     transform: scale(1.2);
        // }
        @include hover-btn();
        &:hover {
            box-shadow: 0 0 2px 0.2px white;
        }
    }

    &:hover .remove-planet {
        display: revert;
        animation: fade-in 0.25s forwards;
    }
}

@keyframes fade-in {
    0% {
        transform: scale(0);
    }
    85% {
        transform: scale(1.07);
    }
    100% {
        transform: scale(1);
    }
}
