/* You can add global styles to this file, and also import other style files */
 * {
    box-sizing: border-box;
 }
 


 


@mixin hover-btn($speed:0.2s) {
    transition: transform $speed;

    &:hover,
    &:focus {
        transform: scale(1.2);
    }

    @content
}

@import "./app/cmps/planet-preview/planet-preview.component.scss";
body {
    color: #c6c6c6;

    font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande", "Lucida Sans Unicode", Geneva, Verdana,
        sans-serif;
    margin: 0;
    background-image: url(https://images.unsplash.com/photo-1507499739999-097706ad8914?ixid=MnwxMjA3fDB8MHxwaG90by1wYWdlfHx8fGVufDB8fHx8&ixlib=rb-1.2.1&auto=format&fit=crop&w=2158&q=80);
    background-size: cover;
}

button {
    cursor: pointer;
}

.btn {
    background-color: black;
    color: #c6c6c6;
    border: 1px solid black;
    border-radius: 2px;
    padding: 2px;

}
.container {
    max-width: 1500px;
    max-width: 95vw;
    // padding: 10px;
}

.main-container {
    display: grid;
    grid-template-columns: 20px 1fr 20px;

    > * {
        grid-column: 2;

        &.full {
            grid-column: 1 /-1;
        }
    }

    
}

.card-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    grid-gap: 20px;
}